let gameURL;

async function serverComm(URL, fieldData) {
  gameURL = URL;
  let response = await fetch(URL, {
    method: "POST",
    body: JSON.stringify(fieldData),
  });

  let result = await response.json();
  // console.log(result);
  return result;
}

async function sendGridArray(fieldData) {
  console.log(gameURL)
  const reqResult = serverComm(gameURL, fieldData).then((result) => result);
  console.log(reqResult)
  return reqResult;
}

export {
  serverComm,
  sendGridArray
};
