import removeElem from "./helpers";
import sendGridArray from "./serverComm";

function Cell(x, y, z, value) {
  this.x = x;
  this.y = y;
  this.z = z;
  this.value = value;
}

function generateField(elemCoefficient) {
  const fieldSize = elemCoefficient;
  const gridArray = [];

  for (let i = -fieldSize; i < fieldSize + 1; i += 1) {
    for (let j = -fieldSize; j < fieldSize + 1; j += 1) {
      for (let k = -fieldSize; k < fieldSize + 1; k += 1) {
        if (i + j + k == 0) {
          gridArray.push(new Cell(i, j, k, 0));
        }
      }
    }
  }
  // console.log(gridArray);
  return gridArray;
}

function drawGrid(gridArray, elemCoefficient) {
  const field = document.querySelector(".game-field");
  const fieldWidth = field.offsetWidth;
  const fieldHeight = field.offsetHeight;

  const elemRadius = elemCoefficient * 2 - 1;
  const cellSize = fieldWidth / (elemCoefficient * 4 + 1);

  const cellWidth = (cellSize * 2).toFixed(2);
  const cellHeight = ((cellWidth * Math.sqrt(3)) / 2).toFixed(2);
  const divElem = document.createElement("div");
  divElem.className = "hex-grid";
  divElem.innerHTML = ``;

  let cellClass = "css-2";

  switch (elemCoefficient) {
    case 2:
      cellClass = "css-3";
      break;
    case 3:
      cellClass = "css-4";
  }

  let x, y, z, value;
  let posX, posY;
  let centerX = fieldWidth / 2;
  let centerY = fieldHeight / 2;

  for (let i = 0; i < gridArray.length; i++) {
    [x, y, z, value] = [
      gridArray[i].x,
      gridArray[i].y,
      gridArray[i].z,
      gridArray[i].value,
    ];
    posX = (x * 3 * cellWidth) / 4 + centerX - cellWidth / 2;
    posY = ((-y + z) * cellHeight) / 2 + centerY - cellHeight / 2;
    divElem.innerHTML += `
    <div class="${cellClass}" 
      data-value="${value}" 
      data-x="${x}" 
      data-y="${y}" 
      data-z="${z}" 
      style="left: ${posX}px; top: ${posY}px;">${value || ""}</div>
    `;
  }
  if (!!document.querySelector(".hex-grid")) {
    removeElem(".hex-grid");
  }
  field.append(divElem);
}

function updateCells(gridArray, dataArray) {
  gridArray.forEach((element) => {
    dataArray.forEach((data) => {
      const result =
        element.x === data.x && element.y === data.y && element.z === data.z;
      if (!!result) {
        element.value = data.value;
      }
    });
  });
}

let elemCoefficient = 1;
let hexGridArray = [];

function initField(inputRadius, data) {
  elemCoefficient = parseInt(inputRadius) - 1;
  hexGridArray = generateField(elemCoefficient);
  updateCells(hexGridArray, data);
  // setGridArray(hexGridArray);
  drawGrid(hexGridArray, elemCoefficient);
  return hexGridArray;
}

function getRowArray(axis, index, array) {
  const rowArray = [];
  array.forEach((element) => {
    if (element[axis] === index) {
      rowArray.push(element);
    }
  });
  return rowArray;
}

// **
// direction Boolean  - true for positive / false for negative

function getMovedRow(rowArray, direction) {
  const initialArray = [...rowArray];
  const filteredInitArr = initialArray.filter(elem => elem.value > 0);
  const valuesArray = filteredInitArr.length && filteredInitArr.map(elem => elem.value) || [];
  const countZeroes = initialArray.length - valuesArray.length;
  const zeroesArray = new Array(countZeroes).fill(0);
  const newArray = direction ? [...zeroesArray, ...valuesArray] : [...valuesArray, ...zeroesArray];
  const moveArr = [...rowArray];
  for (const [index, value] of moveArr.entries()) {
    value.value = newArray[index];
  }
  return moveArr;
}

function getCalculatedRow(rowArray, direction) {
  const initArr = [...rowArray];
  // console.log(`111: ${JSON.stringify(initArr)}`);
  if (direction) {
    for (let i = initArr.length - 1; i > 0; i--) {
      if (initArr[i].value === initArr[i - 1].value) {
        initArr[i].value += initArr[i - 1].value;
        initArr[i - 1].value = 0;
      }
    }
  } else {
    for (let i = 1; i < initArr.length; i++) {
      if (initArr[i - 1].value === initArr[i].value) {
        initArr[i - 1].value += initArr[i].value;
        initArr[i].value = 0;
      }
    }
  }
  // console.log(`222: ${JSON.stringify(initArr)}`);
  return initArr;
}

function getUpdate(axis, fieldRadius, direction) {
  const indexArr = [];
  for (let i = -fieldRadius + 1; i < fieldRadius; i++) {
    indexArr.push(i);
  }
  let movedHexGridArray = [];
  let calculatedHexGridArray = [];
  let updatedHexGridArray = [];

  for (let indexVal of indexArr) {
    let rowArray = getRowArray(axis, indexVal, hexGridArray);
    let movedRow = getMovedRow(rowArray, direction);
    movedHexGridArray.push(...movedRow);
  }

  for (let indexVal of indexArr) {
    let rowArray = getRowArray(axis, indexVal, movedHexGridArray);
    let calculatedRow = getCalculatedRow(rowArray, direction);
    calculatedHexGridArray.push(...calculatedRow);
  }

  // for (let indexVal of indexArr) {
  //   let rowArray = getRowArray(axis, indexVal, calculatedHexGridArray);
  //   let movedRow = getMovedRow(rowArray, direction);
  //   updatedHexGridArray.push(...movedRow);
  // }

  sendGridArray(calculatedHexGridArray);

  const newCells = sendGridArray(calculatedHexGridArray).then(result => result);

  hexGridArray = calculatedHexGridArray;

  // console.log(`333: ${JSON.stringify(hexGridArray)}`);
  // console.log(`444: ${JSON.stringify(updatedHexGridArray)}`);
  drawGrid(calculatedHexGridArray, elemCoefficient);
  return hexGridArray;
}

export {
  initField,
  updateCells,
  drawGrid,
  getUpdate
};
