import {serverComm} from "./modules/serverComm";
import {
  initField as gameField,
  getUpdate
} from "./modules/gameField";

const baseURL = "http://51.15.207.127:13337";
let fieldData = [];
let fieldRadius;
let hexGridArray = [];

const body = document.querySelector("body");
body.addEventListener("click", function (e) {
  const target = e.target;
  if (!!target.closest(".radius-btn")) {
    fieldRadius = target.value;
    initialDataReq(target);
  }
});

function initialDataReq(target) {
  const reqURL = `${baseURL}/${target.value}`;

  serverComm(reqURL, fieldData).then((result) =>
    hexGridArray = gameField(target.value, result)
  );
}

function logKey(e) {
  console.log(e.code);
}

function getDirection(e) {
  logKey(e);
  switch (e.code) {
    case "KeyW":
      // direction = "x+";
      hexGridArray = getUpdate("x", fieldRadius, true);
      break;
    case "KeyS":
      // direction = "x-";
      hexGridArray = getUpdate("x", fieldRadius, false);
      break;
    case "KeyE":
      // direction = "y+";
      hexGridArray = getUpdate("y", fieldRadius, true);
      break;
    case "KeyA":
      // direction = "y-";
      hexGridArray = getUpdate("y", fieldRadius, false);
      break;
    case "KeyD":
      // direction = "z+";
      hexGridArray = getUpdate("z", fieldRadius, true);
      break;
    case "KeyQ":
      // direction = "z-";
      hexGridArray = getUpdate("z", fieldRadius, false);
      break;
    default:
      return;
  }
  console.log(JSON.stringify(hexGridArray));
}

document.addEventListener("keydown", getDirection);

